import store from "@/store"

export default {
    donateBalanceOf(token, account) {
        if (store.state.donate.contract === null) {
            return
        }
        return store.state.donate.contract.donateBalanceOf(token, account)
    },
    donate(token, amount) {
        if (store.state.donate.contract === null) {
            return
        }
        return store.state.donate.contract.connect(store.state.provider.getSigner()).donate(token, amount)
    },
    totalDonateBalance(token) {
        if (store.state.donate.contract === null) {
            return
        }
        return store.state.donate.contract.totalDonateBalance(token)
    },
    withdraw(token) {
        if (store.state.donate.contract === null) {
            return
        }
        return store.state.donate.contract.connect(store.state.provider.getSigner()).withdraw(token)
    }
}
