<template>
  <sec-card :show-background="true" style="width: 100%; max-width: 560px; height: 100%;">
    <div slot="header" class="sec-card-header">
      <div>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-donate"></use>
        </svg>
        <span style="margin-left: 10px;">{{$t("donate.header.title")}}</span>
      </div>
    </div>
    <div>
      <div>
        <span class="tip-span">{{$t('donate.form.donateAmount')}}: </span>
        <span class="tip-span">{{donateAmount}} {{tokenSymbol}}</span>
      </div>
      <div class="sec_form_panel">
        <div class="sec_form_item">
          <div class="sec_form_item_title">
            <span>
              {{$t('donate.form.amount')}}:
            </span>
          </div>
          <div class="sec_form_item_content">
            <span class="tip-span">
              {{$t('donate.form.balance')}}: {{tokenBalanceInUnit}} {{tokenSymbol}}
              <a class="link-a" @click="goMax">({{$t('common.max')}})</a>
            </span>
            <amount-input style="min-width: 300px; margin-top: 5px;" v-model="tradeForm.amount" :symbol="tokenSymbol" :decimals="tokenDecimals"></amount-input>
          </div>
        </div>
      </div>
      <div style="width: 100%; margin-top: 20px; text-align: center;">
        <tx-commit-button :tx-loading="txLoading" :tx-form-check="txFormCheck"></tx-commit-button>
      </div>
    </div>

    <el-dialog
        :visible.sync="txConfirmDialogVisible"
        custom-class="wallet-dialog"
        :title="$t('common.txConfirmDialogTitle')"
        width="90%">
      <tx-confirm ref="txConfirm" v-model="hash" :tx-form="txForm" :call-func="txConfirmCall">
        <div style="width: 100%;" slot="content">
          <div style="width: 100%; text-align: center;" v-if="needApprove">
            {{$t('common.approve')}} {{tokenSymbol}}
          </div>
          <div class="sec_form_panel" v-else>
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('orderNew.form.amount')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{tradeForm.amount}} {{tokenSymbol}}
              </div>
            </div>
          </div>
        </div>
        <span slot="info" style="font-size: 20px;">
          {{ needApprove ? $t('common.approve') + tokenSymbol : formatTradeInfo}}
        </span>
      </tx-confirm>
    </el-dialog>
  </sec-card>
</template>
<script>
import TxCommitButton from '@/components/common/tx_commit_button'
import AmountInput from '@/components/common/amount_input'
import TxConfirm from '@/components/tx/tx_confirm'
import SecCard from '@/components/sec_card'
import base from "@/utils/base"
import tx_base from "@/utils/tx_base";
import common from "@/utils/common";
import erc20 from "@/utils/erc20";
import donate from "@/utils/donate";
import events from "@/utils/events";
export default {
  mixins: [base, tx_base],
  name: 'donate',
  props: {
  },
  components: {
    SecCard,
    TxConfirm,
    AmountInput,
    TxCommitButton
  },
  computed: {
    amountInWei() {
      const amountInWei = common.unitToWei(this.tradeForm.amount.toString())
      return amountInWei
    },
    sellBtnTitle() {
      if (this.initLoading) {
        return this.$t('common.loading')
      }
      if (this.account === '') {
        return this.$t('header.connectWallet')
      } else if (this.amountInWei <= 0) {
        return this.$t('donate.btn.inputAmount')
      } else if (this.needApprove) {
        return this.$t('common.approve')
      }
      return this.$t('donate.btn.donate')
    },
    needApprove() {
      if (this.tradeForm.amount === '') {
        return false
      }
      if (this.amountInWei <= 0) {
        return false
      }
      if (this.allowance === 0) {
        return true
      }
      if (this.allowance.lt(this.amountInWei)) {
        return true
      } else {
        return false
      }
    },
    formatTradeInfo() {
      return `${this.$t('donate.header.title')} ${this.tradeForm.amount} ${this.tokenSymbol}`
    }
  },
  data() {
    return {
      allowance: 0,
      donateAmount: 0,
      tradeForm: {
        amount: '',
      },
      txConfirmDialogVisible: false
    }
  },
  watch: {
  },
  created() {
    this.eventHandlers[events.txConfirmed] = async param => {
      if (param.type === common.getTxTypes().donate) {
        await this.getDonateAmount()
      }
      if (param.type === common.getTxTypes().approve && this.hash === param.hash) {
        await this.getAllowance()
      }
      if (this.hash === param.hash) {
        this.txPending = false
      }
    }
    this.eventHandlers[events.txSent] = param => {
      if (this.hash === param && this.txForm.type === common.getTxTypes().donate) {
        this.tradeForm.amount = ''
      }
    }
    this.initData()
  },
  methods: {
    async baseInit() {
      await this.getDonateAmount()
      await this.getAllowance()
    },
    initData() {
      // this.tradeForm.amountPercent = 0
      // this.tradeForm.amount = 0
    },
    async getDonateAmount() {
      const amount = await donate.donateBalanceOf(this.tokenAddress, this.account)
      this.donateAmount = common.weiToUnit(amount, this.tokenDecimals)
    },
    async getAllowance() {
      this.allowance = await erc20.allowance(this.account, this.donateItem.contract.address)
    },
    goMax() {
      this.tradeForm.amount = this.tokenBalanceInUnit
    },
    txFormCheck() {
      let r = {
        success: false
      }
      if (this.amountInWei <= 0) {
        r.msg = this.$t('donate.btn.inputAmount')
        return r
      }
      if (this.amountInWei.gt(this.tokenBalance)) {
        r.msg = this.$t('common.balanceNotEnough')
        return r
      }

      if (this.needApprove) {
        r.msg = this.$t('common.approve')
        r.success = true
        r.func = this.goDonate
        return r
      }
      r.msg = this.$t('donate.btn.donate')
      r.success = true
      r.func = this.goDonate
      return r
    },
    async goDonate() {

      if (this.tradeForm.amount > this.tokenBalanceInUnit) {
        this.$message.warning(this.$t('common.balanceNotEnough'))
        return
      }

      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }

      if (this.needApprove) {
        this.txForm = {
          type: common.getTxTypes().approve,
          token: this.tokenItem.address,
          to: this.donateItem.contract.address,
          sender: this.account,
        }
        this.txConfirmCall = () => {
          return erc20.approve(this.txForm.to, -1)
        }
      } else {

        this.txForm = {
          type: common.getTxTypes().donate,
          token: this.tokenItem.address,
          from: this.account,
          to: this.donateItem.contract.address,
          amount: common.unitToWei(this.tradeForm.amount, this.tokenItem.decimals),
        }
        this.txConfirmCall = () => {
          return donate.donate(
              this.txForm.token,
              this.txForm.amount
          )
        }
      }

      this.txConfirmDialogVisible = true
    },
  }
}
</script>
